<template>
    <nav class="navbar mb-5 navbar-expand-lg navbar-light bg-light">
        <div class="container-fluid">
            <router-link :to="{ name: 'Home' }" tag="a" class="navbar-brand">Shop</router-link>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div class="navbar-nav">
                    <router-link :to="{ name: 'Category', params: { id: category.id } }" class="nav-item nav-link"
                                 v-for="(category, key) in categories" :key="key">
                        {{ category.name }}
                        <span v-if="'num_of_products' in category">{{ category.num_of_products }}</span>
                        <span v-else>0</span>
                    </router-link>
                </div>
                <div class="navbar-nav ml-auto" v-if="!auth">
                    <router-link :to="{ name: 'Auth.SignIn' }"
                                 class="nav-item nav-link"
                                 v-if="!$root.UserResource.auth()">Войти</router-link>
                    <router-link :to="{ name: 'Auth.SignUp' }"
                                 class="nav-item nav-link"
                                 v-if="!$root.UserResource.auth()">Регистрация</router-link>
                </div>
                <div class="navbar-nav ml-auto" v-else>
                    <ul class="navbar-nav">
                        <li class="nav-item dropdown">
                            <a class="nav-link dropdown-toggle" href="http://example.com" id="navbarDropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                {{ user.email }}
                            </a>
                            <div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                                <a class="dropdown-item" href="#" @click="logout()">Выйти</a>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </nav>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
  name: 'navbar',
  computed: {
    ...mapState('categoryModule', {
      categories: state => state.categories
    }),
    ...mapState('userModule', {
      auth: state => state.auth,
      user: state => state.info
    })
  },
  created () {
    this.loadCategories()
  },
  methods: {
    ...mapActions('categoryModule', [
      'loadCategories'
    ]),
    ...mapActions('userModule', [
      'logout'
    ])
  }
}
</script>



