import { Axios } from '@/scripts/_axios'
import Vue from 'vue'
import VueLocalStorage from 'vue-localstorage'

Vue.use(VueLocalStorage)

const auth = Vue.localStorage.get('auth')

export function Api () {
  const baseURL = process.env.API_URL
  return Axios(
    baseURL,
    10000,
    {
      'Authorization': 'Bearer ' + auth
    }
  )
}



